import { Component, OnInit } from '@angular/core';
import { EmployeeService } from 'src/app/core/services/employee.service';
import { NbAuthService, NbAuthJWTToken } from '@nebular/auth';
import { UserPayload } from 'src/app/core/models/userPayload.model';
import { Employee } from 'src/app/core/models/employee.model';
import { NbDialogRef, NbToastrService } from '@nebular/theme';
import { PasswordChange } from 'src/app/core/models/passwordChange.model';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { ParameterService } from 'src/app/core/services/parameter.service';
import * as moment from "moment";

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})

export class ProfileComponent implements OnInit {
  startDateP = new Date();
  birthdayP = new Date();
  ProfilForm:FormGroup;
  PassForm:FormGroup;
  submitted = false;
  changed=false;
  user:UserPayload;
  employee=new Employee();
  passwordchange = new PasswordChange();
  newPassword;
  oldPassword;
  showHide=0;
  isMale=0; // for blank photo
  civilStatuss;
  civilStatu;

  constructor( private employeeservice:EmployeeService, 
    private authService: NbAuthService,
    protected ref: NbDialogRef<ProfileComponent>,
    private toastrService: NbToastrService,
    private formBuilder:FormBuilder,
    private parameterService:ParameterService) 
    {
    this.authService.onTokenChange()
    .subscribe((token: NbAuthJWTToken) => {

      if (token.isValid()) {
        this.user = token.getPayload(); // here we receive a payload
        // from the token and assigne it to our `user` variable
      }

    });
  }

  ngOnInit() {
    this.employeeservice.getById(this.user.id).subscribe((data) => {
      this.employee = data;
    });
    this.parameterService.getByGroup("CivilStatus")
      .subscribe(data => this.civilStatuss = data);
    this.ProfilForm= this.formBuilder.group({
      firstName:['',Validators.required],
      lastName:['',Validators.required],
      email:['',Validators.required],
      function:['',Validators.required],
      cin:['',Validators.required],
     // birthday:['',this.DateValidator()],
      adress:['',Validators.required],
      city:['',Validators.required],
      phoneNumber:['',Validators.required],
      registrationNumber:['',Validators.required],
     // startDate:['',this.DateValidator()],
      civilStatus:[]
    });
    this.PassForm=this.formBuilder.group({
      currentPassword:['',[Validators.required,Validators.minLength(6)]],
      newPassword:['',[Validators.required,Validators.minLength(6)]],
      userName:[]
    })
  }
  get f() { return this.ProfilForm.controls; }
  get ff() { return this.PassForm.controls; }

  dismiss() {
    this.ref.close();
  }

  update(){
    this.submitted = true;
    if (this.ProfilForm.invalid) {
      return;}
      //this.employee.birthday=this.birthdayP.toDateString();
      //this.employee.startDate=this.startDateP.toDateString();
      this.employee.civilStatus=this.civilStatu;
      console.log(this.employee)
    this.employeeservice.update(this.employee)
    .subscribe(()=> {this.showHide=0});
  }

  changePassword(){
    this.changed = true;
    if (this.PassForm.invalid) {
      return;}
    this.passwordchange.userId=this.employee.id;
    this.passwordchange.newPassword=this.newPassword;
    this.passwordchange.oldPassword=this.oldPassword;
    this.employeeservice.changePassword(this.passwordchange).subscribe(
      ()=>{
        this.showToast('Password updated successfully', 'Success');},
        (error)=>{ this.toastrService.danger(status, 'Old password is invalid');}
      );
  }

  showToast(status, message) {
    this.toastrService.success(status, message, { status });
  }
  DateValidator(format = "dd/MM/YYYY"): any {
    return (control: FormControl): { [key: string]: any } => {
      const val = moment(control.value, format, true);
  
      if (!val.isValid()) {
        return { invalidDate: true };
      }
  
      return null;
    };

}}
